  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.header .container .content .button a {
  color: #ae0f0f;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
}

.header .container .content .button:hover {
  color: red;
  background: #1d1d1d;
  transition: all .4s;
}

@media screen and (max-width: 1200px) {
  body._lock {
    overflow: hidden;
  }

  .header .container {
    margin-top: 0;
  }

  .header .container._menu-backgraund:before {
    content: "";
    width: 100%;
    height: 50px;
    z-index: 11;
    background-color: #000;
    position: fixed;
    top: 0;
    left: 0;
  }

  .header .container .logo {
    width: 52vw;
    margin-left: 2%;
  }

  .header .container .logo img {
    width: 28px;
  }

  .header .container .logo h3 {
    font-size: 20px;
  }

  .header .container .navigation {
    z-index: 10;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .9);
    padding: 90px 20px 20px;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: -100%;
    overflow: auto;
  }

  .header .container .navigation._active {
    left: 0;
  }

  .header .container .navigation .nav {
    flex-direction: column;
  }

  .header .container .navigation .nav li {
    margin: 0 0 30px;
  }

  .header .container .navigation .nav li:last-child {
    margin-bottom: 0;
  }

  .header .container .content {
    gap: 20px;
    margin-right: 2%;
  }

  .header .container .content .search button img {
    width: 24px;
  }

  .header .container .content .burger {
    opacity: 1;
    z-index: 11;
    width: 30px;
    height: 18px;
    cursor: pointer;
    display: block;
    position: relative;
  }

  .header .container .content .burger span, .header .container .content .burger:before, .header .container .content .burger:after {
    height: 10%;
    width: 100%;
    background-color: #fff;
    transition: all .3s;
    position: absolute;
    left: 0;
  }

  .header .container .content .burger:before, .header .container .content .burger:after {
    content: "";
  }

  .header .container .content .burger:before {
    top: 0;
  }

  .header .container .content .burger:after {
    bottom: 0;
  }

  .header .container .content .burger span {
    top: 50%;
    transform: scale(1)translate(0, -50%);
  }

  .header .container .content .burger._active span {
    transform: scale(0)translate(0, -50%);
  }

  .header .container .content .burger._active:before {
    top: 50%;
    transform: rotate(-45deg)translate(0, -50%);
  }

  .header .container .content .burger._active:after {
    bottom: 50%;
    transform: rotate(45deg)translate(0, 50%);
  }

  .header .container .content .button {
    width: 106px;
    height: 35px;
  }

  .header .container .content .button a {
    font-size: 11px;
  }

  .header .container .content .button img {
    width: 30px;
  }
}

@media screen and (max-width: 500px) {
  .header .container .content {
